@media screen and (max-width: 1200px) {
  .info-hotspot-modal {
    padding: 1rem 2rem;
  }
  .info-hotspot-modal .info-hotspot-content,
  .info-hotspot-modal .info-hotspot-header {
    max-width: 800px;
  }
}

@media (min-width: 1440px) {
  .link-hotspot-tooltip {
    left: -87.5%;
    font-size: 1rem;
    width: 140px;
  }
  #fullscreenToggle .icon {
    width: 24px;
    height: 24px;
    -webkit-filter: invert(1);
    filter: invert(1);
  }
  #titleBar {
    height: 70px;
  }
  #titleBar .sceneName {
    max-width: 150px;
  }
}

@media only screen and (max-width: 650px) {
  .link-hotspot-icon {
    width: 30px;
    height: 30px;
    right: 1rem;
  }

  #tooltip,
  #tooltip-LH {
    font-size: 0.65rem;
  }

  #next,
  #previous {
    max-height: 20px;
    max-width: 20px;
  }

  body
    > div.info-hotspot-modal
    > div.info-hotspot-content
    > div.product-txt-container
    > div
    > div.product-txt {
    font-size: 0.86rem;
  }

  body
    > div.info-hotspot-modal
    > div.info-hotspot-content
    > div.product-txt-container
    > div
    > div.recipes {
    margin-top: 1rem;
  }
  #titleBar {
    height: 30px;
    padding-left: 0;
    padding-right: 0;
  }
  .sceneName {
    width: 70px;
  }
  #fullscreenToggle * {
    width: 50%;
    margin-left: 1rem;
  }
  #footer {
    height: 30px;
    padding: 0 5px 0 0;
  }
  #footer svg {
    padding-top: 5px;
    width: 24px;
    height: 24px;
  }
  .controls {
    width: 80%;
  }
  .controls * {
    font-size: 0.5rem;
  }
  .controls-h1 {
    font-size: 1rem;
  }
  .controls-header {
    height: 20px;
  }

  .controls-icons img,
  .controls-section-top img,
  .controls-menu-close img {
    height: 20px;
    width: 20px;
  }
}

