@media only screen and (max-width: 900px) and (max-height: 500px) {
  .info-hotspot-modal .info-hotspot-header {
    margin: 0 0.25rem;
    z-index: 100000;
  }
  .info-hotspot-modal .info-hotspot-title-wrapper {
    display: none;
  }
  .info-hotspot-modal .info-hotspot-close-wrapper {
    position: absolute;
    right: 0.25rem;
    top: 1rem;
    -webkit-filter: invert(0.5);
    filter: invert(0.5);
    mix-blend-mode: screen;
  }

  .info-hotspot-modal .info-hotspot-content {
    max-width: 100%;
    max-height: 100%;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 10px;
  }

  .product-txt-wrapper {
    margin-top: 0;
    padding: 0;
  }

  .product-txt-wrapper .product-txt-subhead {
    margin-top: 20px;
  }
}

@media only screen and (max-width: 500px) and (min-height: 550px) {
  body
    > div.info-hotspot-modal
    > div.info-hotspot-header
    > div.info-hotspot-title-wrapper {
    padding-left: 2px;
  }
  .info-hotspot-modal .info-hotspot-content {
    top: 90px;
  }
}

@media only screen and (max-width: 500px) and (max-height: 384px) and (min-height: 384px) {
  body > div.info-hotspot-modal > div.info-hotspot-header {
    top: 5;
  }
}

@media only screen and (max-width: 500px) and (min-height: 500px) {
  .carousel_container {
    -ms-grid-row: 1;
    -ms-grid-row-span: 1;
    grid-row: 1 / span 1;
  }
  .product-txt-container {
    -ms-grid-row: 2;
    -ms-grid-row-span: 1;
    grid-row: 2 / span 1;
    display: block;
  }
}

@media only screen and (max-width: 500px) {
  .info-hotspot-modal .info-hotspot-header {
    top: 0;
  }
  .product-txt-wrapper {
    padding: 0;
  }
  .info-hotspot-modal .info-hotspot-content {
    max-width: 100%;
    max-height: 100%;
    left: 10px;
    right: 10px;
    bottom: 10px;
  }
  #help-menu .help-content {
    width: 80%;
  }

  .carousel-nav .carousel-button {
    max-width: 15px;
    max-height: 15px;
  }

  #help-body .help-list {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-box-pack: space-evenly;
    -ms-flex-pack: space-evenly;
    justify-content: space-evenly;
  }

  #help-body {
    width: 100%;
  }
  .controls-content {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    max-width: 300px;
  }

  .product-txt-wrapper > *:not(.product-txt) {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  .info-hotspot-modal .info-hotspot-title {
    font-size: 1.5rem;
  }

  .info-hotspot-modal .info-hotspot-title-wrapper {
    -ms-grid-column-align: start;
    justify-self: start;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: start;
    padding-left: 10px;
  }

  .carousel_container .influencer_credit_container {
    bottom: 4%;
  }

  .carousel_container .influencer_credit_container {
    bottom: 7.5%;
  }

  .carousel_container .pointer {
    top: -20%;
    width: calc(25px / 2);
    height: calc(9.5px / 2);
    background: var(--secondary-white);
    -webkit-clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
  }

  body
    > div.info-hotspot-modal
    > div.info-hotspot-content
    > div.product-txt-container
    > div
    > div.recipes
    > h2 {
    font-size: 1rem;
    padding-bottom: 0.5rem;
  }

  body
    > div.info-hotspot-modal
    > div.info-hotspot-content
    > div.product-txt-container
    > div
    > h2 {
    font-size: 1.5rem;
  }
  body
    > div.info-hotspot-modal
    > div.info-hotspot-content
    > div.product-txt-container
    > div
    > div.product-txt {
    font-size: 0.75rem;
  }
}

